.App {

  text-align: center;

  font-family: Arial, sans-serif;

  padding: 20px;

  background-color: #2c2c2c; /* Антрацитовый цвет */

  color: white; /* Белый цвет текста для контраста */

  min-height: 100vh;

  display: flex;

  flex-direction: column;

  align-items: center;

}


input {

  padding: 10px;

  font-size: 16px;

  margin-right: 10px;

  border: none;

  border-radius: 4px;

}


button {

  padding: 10px 20px;

  font-size: 16px;

  cursor: pointer;

  border: none;

  border-radius: 4px;

  background-color: #4caf50; /* Зеленый цвет кнопки */

  color: white;

}


button:hover {

  background-color: #45a049; /* Более темный зеленый при наведении */

}


.weather {

  margin-top: 20px;

  padding: 20px;

  border: 1px solid #ccc;

  border-radius: 8px;

  display: inline-block;

  text-align: left;

  background-color: #ffeb3b; /* Желтый цвет */

  color: black; /* Черный цвет текста для контраста */

}